    <section>
      <h1>{{ count1 }}</h1>
    </section>

    <div id="coco">{{ counter }}</div>
  </div>
</template>

<script>
import AboutPage from "@/components/AboutPage.vue";

export default {
  data() {
    return {
      counter: 0,
      count: 0,
      count1: 0,
      ScrollDown: null,
    };
  },
  mounted() {
    this.increase();
    document;
    window.addEventListener("focusin", this.onscroll);
    this.onscroll();
    clearInterval();
  },
  methods: {
    increase() {
      setInterval(() => {
        this.ScrollDown = window.scrollY;
        if (this.ScrollDown >= 950 && this.count <= 110) {
          this.count++;
        }
      }, 50);
      // setInterval(() => {
      //   const element = document.getElementById("coco");
      //   element.innerHTML = this.count1++;
      // }, 1000);
    },
    onscroll() {
      setInterval(() => {
        if (this.counter <= 100) {
          this.counter++;
        }
      }, 1000);
    },
  },
  created() {},
  components: { AboutPage },
};
</script>

